<template>
    <view class="activity clear">
		<navbar v-model="screenHeight" :scroll="scrollNum" :iSimmersive="true" title="拼团"></navbar>
		<view class="status_bar">  
			<view class="top_view"></view>  
		</view>  
        <mescroll-body 
		ref="mescrollRef" 
		@init="mescrollInit" 
		@down="downCallback" 
		@up="upCallback" 
		@emptyclick="emptyClick" 
		:height="screenHeight+'px'"
		>
			<view class="group-wrap clear">
					<view class="banner">
						<image v-if="static" :src="static+'app/group.png'"></image>
					</view>

					<view class="list clear">

						<view class="item-box"
							 v-for="(item,index) in result"
							 :key="index"
							 @click="$utils.navigateTo('group/view',{ id: item.id })"
						>
							<view class="image">
								<image :src="item.photo">
							</view>

							<view class="r">
								<view class="t">{{item.title}}</view>
								<view class="price">￥{{item.sell_price}}</view>
								<view class="b">
									<view>去拼团</view>
									<view>{{item.sum_count}}人已拼团</view>
								</view>
							</view>
						</view>
					</view>
			</view>
		</mescroll-body>
    </view>
</template>

<script>
	import MescrollMixin from "@/uni_modules/mescroll-uni/components/mescroll-uni/mescroll-mixins.js";
	import navbar from "@/components/navbar/navbar";
	export default {
		mixins: [MescrollMixin],
		components:{
			navbar
		},
		data() {
			return {
				screenHeight: 0,
				static: "",
				scrollNum: 0,
				result: []
			}
		},
		onLoad() {
			this.static = this.$static;
		},
		onShow(){
			//#ifdef APP-PLUS
			// 设置系统状态栏样式为深色前景色样式
			plus.navigator.setStatusBarStyle("light");
			//#endif
		},
		onReady(){
			//#ifdef APP-PLUS
			// 设置系统状态栏样式为深色前景色样式
			plus.navigator.setStatusBarStyle("light");
			//#endif
		},
		onPageScroll(obj){
			// 在滑动页面时将改变状态栏颜色
			// 设置系统状态栏样式为深色前景色样式
			//console.log("mescroll-body的滚动事件e.scrollTop=" + e.scrollTop);
			this.scrollNum = obj.scrollTop;
			//#ifdef APP-PLUS
			if(obj.scrollTop >= 100){
				plus.navigator.setStatusBarStyle("dark");
			}else{
				plus.navigator.setStatusBarStyle("light");
			}
			//#endif
		},
		//#ifdef APP-PLUS
		onTabItemTap : function (obj){ // index,pagePath,text
			// 设置系统状态栏样式为深色前景色样式
			if(obj.index == 0){
				if(this.scrollNum >= 100){
					plus.navigator.setStatusBarStyle("dark");
				}else{
					plus.navigator.setStatusBarStyle("light");
				}
			}
		},
		//#endif
		methods: {
			emptyClick(){
				this.$utils.switchTab("index/index");
			},
			downCallback(){
				setTimeout(()=>{
					this.mescroll.resetUpScroll();
				},200);
			},
			// 主动触发下拉刷新
			triggerDownScroll(){
				this.mescroll.triggerDownScroll();
			},
			upCallback(page) {
				this.$http.getGroupList({
					page: page.num,
				}).then((result)=>{
					this.mescroll.endByPage(result.data.list.length, result.data.total);
					if(result.status==1){
						if(page.num == 1) this.result = [];
						this.result = this.result.concat(result.data.list);
					}else if(result.status == -1){
						this.mescroll.endErr();
					}
				}).catch(error=>{
					this.mescroll.endErr();
				});
			}
		}
	}
</script>

