                    width: 160rpx;
                    height: 160rpx;
                    background-color: #e9e9e9;
                    image {
                        width: 160rpx;
                        height: 160rpx;
                    }
                }
                .r{
                    .t{
                        display: block;
                        font-size: 32rpx;
                        color: #666;
